@import "./card_variables.scss";

.text {
  width: max-content;

  &--size_10 {
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
  }

  &--size_12 {
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
  }

  &--size_16 {
    font-weight: 700;
    font-size: 16px;
    line-height: 18px;
  }
}
